.checkout-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 70px;
    padding-top: 50;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
}

.form-group {
    margin-bottom: 20px;
}

label {
    display: block;
    font-weight: bold;
}

.error {
    color: red;
}

.confirmation-message {
    margin-top: 20px;
    padding: 10px;
    background-color: #dff0d8;
    border: 1px solid #d6e9c6;
    border-radius: 4px;
}

@media (max-width: 1024px) {
    /* Add styles for screens smaller than 1024px */
    .checkout-container {
        max-width: 500px;
    }
}

@media (max-width: 500px) {
    /* Add styles for screens smaller than 500px */
    .checkout-container {
        max-width: 300px;
    }
}