.navbar {
    display: flex;
    justify-content: space-between; /* Ensure the content is spaced between */
    padding: 20px; /* Reduced padding */
    box-shadow: 0 1px 3px -2px black;
    align-items: center;
    flex-wrap: wrap; /* Allow wrapping for smaller screens */
}

.nav-logo {
    display: flex;
    align-items: center;
    gap: 10px;
}


.nav-logo p {
    color: #171717;
    font-size: 25px; /* Reduced font size */
    font-weight: 600;
}

.nav-menu {
    display: flex;
    align-items: center;
    list-style: none;
    gap: 15px; /* Adjusted gap for better spacing */
    color: #626262;
    font-size: 13px; /* Decreased font size */
    font-weight: 500;
    flex-wrap: wrap; /* Allow wrapping for smaller screens */
    justify-content: center; /* Center items */
    padding-left: 10px;
}

.nav-menu li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}

.nav-menu hr {
    border: none;
    width: 1px; /* Adjusted separator width */
    height: 15px; /* Adjusted separator height */
    background-color: #626262; /* Separator color */
}

.nav-login-cart {
    display: flex;
    align-items: center;
    gap: 20px; /* Reduced gap */
    flex-wrap: nowrap; /* Ensure the elements stay in one line */
}

.nav-login-cart button {
    width: 130px; /* Adjusted width */
    height: 40px; /* Adjusted height */
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 20px; /* Reduced border radius */
    color: #515151;
    font-size: 14px; /* Reduced font size */
    font-weight: 500;
    background: white;
    cursor: pointer;
}

.nav-login-cart button:active {
    background: #f3f3f3;
}

.nav-cart-count {
    width: 20px; /* Adjusted size */
    height: 20px; /* Adjusted size */
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -10px; /* Adjusted position */
    margin-left: -15px; /* Adjusted position */
    border-radius: 10px; /* Adjusted border radius */
    font-size: 12px; /* Adjusted font size */
    background: red;
    color: white;
}

.nav-dropdown {
    display: none;
}

@media (max-width: 1280px) {
    /* Styles remain unchanged */
}

@media (max-width: 1024px) {
    /* Styles remain unchanged */
}

@media (max-width: 800px) {
    .navbar {
        padding: 10px 20px; /* Adjusted padding */
    }

    .nav-logo {
        transform: scale(0.8);
    }

    .nav-menu {
        flex-direction: row; /* Ensure menu stays in row */
        flex-wrap: wrap;
        gap: 10px; /* Adjusted gap */
    }

    .nav-login-cart {
        transform: scale(0.8);
        gap: 10px; /* Adjusted gap */
    }

    .nav-menu li {
        font-size: 12px; /* Decreased font size for smaller screens */
    }
}

@media (max-width: 500px) {
    .navbar {
        padding: 10px 0; /* Adjusted padding */
        flex-direction: column; /* Stack items vertically on very small screens */
        align-items: flex-start;
    }

    .nav-logo {
        margin-bottom: 10px; /* Add margin below the logo */
    }

    .nav-menu {
        flex-direction: column; /* Stack menu items vertically */
        align-items: flex-start; /* Align menu items to the start */
        gap: 5px; /* Adjusted gap */
    }

    .nav-login-cart {
        margin-top: 10px; /* Add margin above the login/cart section */
        align-items: center;
        justify-content: space-between; /* Spread out the login and cart */
        width: 100%; /* Full width */
    }

    .nav-login-cart button, .nav-cart-count {
        margin: 0; /* Remove extra margins */
    }
}
