.new-books{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-bottom: 100px;
}
.new-books h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}
.new-books hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;

}

.books{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    margin-left: 30px;
    gap: 30px;
}

@media(max-width:1280px){
    
    .new-books h1{
        font-size: 40px;
    }
    .new-books hr{
        width: 160px;
        height: 4px;
    }
    .books{
        gap: 20px;
        margin-top: 30px;
    }
        
}

@media(max-width:1024px){
    
    .new-books h1{
        font-size: 30px;
    }
    .new-books hr{
        width: 120px;
        height: 3px;
    }
    .books{
        gap: 15px;
        margin-top: 20px;
    }
    
}

@media(max-width:800px){
    .new-books{
        gap: 6px;
    }
    .new-books h1{
        font-size: 20px;
    }
    .new-books hr{
        width: 100px;
    }
    .books{
        gap: 5px;
    }
    
}

@media(max-width:500px){
    
    .books{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
    }
}




